.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px;
    background-color:steelblue;
    .logo-app {
        font-size: 22px;
        font-weight: bold;
        color: white;
        margin: 0px;
    }
    .menu-item {
        font-size: 18px;
        color: white;
        text-decoration: underline;
        cursor: pointer;
        margin: 0px;
    }
}