.footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:#72acdd;
    padding: 20px 40px;
    .logo {
        width: 70px;
        height: 70px;
        object-fit: contain;
        padding: 1px;
        // margin: 0px 11px;
        background-color: rgb(71, 89, 148);
        border-radius: 50px;
    }
    .social-wrapper{
        display: flex;
        .icon-wrapper {
            background-color: white;
            padding: 10px;
            border-radius: 40px;
            margin:  0px 10px;
            .icon-medsos {
                width: 30px;
                height: 30px;
                object-fit: contain;
            }
    }
    }
}
.copyright{
    background-color:#4998d9;
    padding: 10px 0px;
    p{
        font-size: 14px;
        color: white;
        text-align: center;
        margin: 0px;
    }
}